<template>
  <mdui-top-app-bar scroll-behavior="elevate" style="background-color: rgba(var(--mdui-color-primary-container, 0.8))">
    <div style="margin-left: 80px"></div>
    <img src="https://s21.ax1x.com/2024/04/07/pFq2AoR.png" alt="alternative" style="width: 2.5rem; height: 2.5rem;">
    <mdui-top-app-bar-title></mdui-top-app-bar-title>
    <mdui-tooltip content="">
      <mdui-button-icon variant="filled" icon="add" id="createStreamBtn"></mdui-button-icon>
    </mdui-tooltip>
    <div style="margin-left: 1px"></div>
    <mdui-tooltip content="">
      <mdui-button-icon variant="filled" icon="refresh" id="refreshBtn"></mdui-button-icon>
    </mdui-tooltip>
  </mdui-top-app-bar>

  <mdui-navigation-rail divider padding-left id="rail" value="stream">
    <mdui-navigation-rail-item icon="videocam" id="streamBtn" value="stream"></mdui-navigation-rail-item>
    <mdui-navigation-rail-item icon="topic" id="materialBtn" value="material"></mdui-navigation-rail-item>
    <mdui-navigation-rail-item icon="help_center" href="https://www.yuque.com/seeds-ejjgd/py7vim" target="_blank" id="groupBtn" value="help"></mdui-navigation-rail-item>
    <mdui-navigation-rail-item icon="group" href="https://qm.qq.com/q/hpYH0xIsuY" target="_blank" id="groupBtn" value="group"></mdui-navigation-rail-item>
    <mdui-navigation-rail-item icon="local_fire_department" href="https://www.beishaoidc.cn/aff/TKYYFUGE" target="_blank" id="adv" value="adv"></mdui-navigation-rail-item>
    <mdui-navigation-rail-item slot="bottom" icon="logout" id="logoutBtn" value="logout"></mdui-navigation-rail-item>
  </mdui-navigation-rail>

  <mdui-container class="mdui-m-t-2">
    <mdui-table-fluid>
      <table class="mdui-table mdui-table-striped">
        <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody id="material-list-tbody">
        </tbody>
      </table>
    </mdui-table-fluid>
  </mdui-container>

  <mdui-segmented-button-group full-width>
    <mdui-segmented-button icon="arrow_backward" id="lastPageBtn"></mdui-segmented-button>
    <mdui-segmented-button id="pageText">11</mdui-segmented-button>
    <mdui-segmented-button end-icon="arrow_forward" id="nextPageBtn"></mdui-segmented-button>
  </mdui-segmented-button-group>

  <mdui-dialog
      id = "deleteDialog"
      close-on-overlay-click
      headline="?"
      class="example-action">
    <mdui-button slot="action" variant="text" id="dialogCancelBtn"></mdui-button>
    <mdui-button slot="action" variant="tonal" id="dialogConfirmBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog
      id = "paymentDialog"
      stacked-actions
      close-on-overlay-click
      headline=""
      class="example-action">
    <div style="display: flex; align-items: center">
      <p></p>
      <div style="flex-grow: 1"></div>
      <h3 id="priceText">0 </h3>
      <div style="flex-grow: 1"></div>
      <mdui-button variant="text" id="useCdkBtn"></mdui-button>
    </div>
    <div style="display: flex; align-items: center;">
      <p style="margin-right: 1px"></p>
      <mdui-slider style="width: 200px" id="monthSlider"></mdui-slider>
    </div>
    <div style="display: flex; align-items: center">
      <p style="margin-right: 1px"></p>
      <div style="flex-grow: 1"></div>
      <mdui-button variant="text" id="paymentUpdateBtn"></mdui-button>
    </div>
    <mdui-radio-group value="hdVideo" id="radio">
      <mdui-radio value="hdVideo"></mdui-radio>
      <mdui-radio value="video"></mdui-radio>
      <mdui-radio value="pic"></mdui-radio>
    </mdui-radio-group>
    <mdui-button slot="action" variant="text" id="dialogAlipayBtn"></mdui-button>
    <mdui-button slot="action" variant="text" id="dialogWechatBtn"></mdui-button>
    <mdui-button slot="action" variant="text" id="cancelPaymentBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog
      id = "changeDialog"
      close-on-overlay-click
      headline=""
      class="example-action">
    <mdui-text-field style="margin-bottom: 16px" label="" id="streamName"></mdui-text-field>
    <mdui-text-field style="margin-bottom: 16px" label="" id="streamUrl"></mdui-text-field>
    <mdui-text-field style="margin-bottom: 16px" label="" id="streamKey"></mdui-text-field>
    <mdui-select id="selectMenu" label="">
    </mdui-select>
    <mdui-button slot="action" variant="text" id="changeCancelBtn"></mdui-button>
    <mdui-button slot="action" variant="tonal" id="changeConfirmBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog
      id = "updateDialog"
      stacked-actions
      close-on-overlay-click
      headline=""
      class="example-action">
    <p></p>
    <h3 id="lastDayText">0 </h3>
    <p></p>
    <h3 id="updatePriceText">0 </h3>
    <mdui-button slot="action" variant="text" id="updateAlipayBtn"></mdui-button>
    <mdui-button slot="action" variant="text" id="updateWechatBtn"></mdui-button>
    <mdui-button slot="action" variant="text" id="cancelUpdateBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog
      id = "cdkDialog"
      close-on-overlay-click
      headline=""
      description=""
      class="example-action">
    <mdui-text-field label="" id="cdkInput" style="margin-bottom: 24px"></mdui-text-field>
    <mdui-button slot="action" variant="text" id="cdkCancelBtn"></mdui-button>
    <mdui-button slot="action" variant="tonal" id="cdkConfirmBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog
      id = "qrcodeDialog"
      headline=""
      class="example-action">
    <div id="qrcode"></div>
    <mdui-button slot="action" variant="text" id="cancelOrderBtn"></mdui-button>
  </mdui-dialog>

  <mdui-dialog fullscreen class="example-fullscreen" id="logDialog">
    <div id="logDiv"></div><br>
    <mdui-button id="logDialogCloseBtn"></mdui-button>
  </mdui-dialog>
</template>

<script>
import init from '../js/stream';
export default {
  mounted() {
    init();
  }
}
</script>

